@charset "utf-8";
@mixin gPage {
	.gPage {
		@include tabletP {
			padding-top: 20px;
		}
	}
}
body#csr {
	@include gPage;
	.myTitleBg {
		background-image:url(../img/csr/img_bg.jpg);
	}
}

body#environment {
	@include gPage;
	.myTitleBg {
		background-image:url(../img/csr/environment/img_bg.jpg);
	}
	.mySection02 {
		.myBlock {
			max-width: 580px;
			margin: 0 auto;
			ol ul {
				margin-top: 10px;;
			}
			.txt {
				text-align: center;
			}
		}
	}
	.mySection03 {
		text-align: center;
		.myBlock {
			margin-bottom: 60px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	.mySection04 {
		.myText {
			margin-bottom: 90px;
		}
		.myBox {
			max-width: 640px;
			margin: 40px auto 0;
			@include tabletP {
				margin-top: 80px;
			}
			.ttl {
				font-weight: bold;
				margin-bottom: 5px;
			}
			.text {
				@include fz(13);
				@include tabletP {
					@include fz(12);
				}
			}
		}
	}
	
	.myBlockTableSlider {
		max-width: 640px;
		margin-right: auto;
		margin-left: auto;
		@include fz(12);
		&__header {
			display: flex;
			border-bottom: 2px solid $mainColor;
			font-weight: bold;
			color:$mainColor;
			.name,
			.day {
				padding: 10px 15px;
				line-height: 1.4;
				@include tabletP {
					padding: 10px 0;
				}
			}
			.name {
				width: calc(100% - 130px);
				@include tabletP {
					width: calc(100% - 90px);
				}
			}
			.day {
				width: 130px;
				text-align: right;
				@include tabletP {
					width: 90px;
				}
			}
		}
		&__item {
			display: flex;
			border-bottom: 1px solid #f7f7f7;
			align-items: center;
			.name,
			.day {
				padding: 10px 15px;
				line-height: 1.4;
				@include tabletP {
					padding: 10px 0;
				}
			}
			.name {
				width: calc(100% - 130px);
				@include tabletP {
					width: calc(100% - 90px);
				}
			}
			.day {
				width: 130px;
				text-align: right;
				@include tabletP {
					width: 90px;
				}
			}
		}
	}
	.slick-list {
		margin: 0 -10px 30px;
	}
	.slick-slide {
		padding: 0 10px;
	}
	.slick-dots {
		text-align: center;
		>li {
			display: inline-block;
			vertical-align: middle;
			padding: 0 5px;
			line-height: 1;
			button {
				width: 15px;
				height: 15px;
				vertical-align: middle;
				overflow: hidden;
				text-indent: -9999px;
				border: none;
				background: rgba(#000,.2);
				border-radius: 100px;
				padding: 0;
			}
			&.slick-active {
				button {
					background: $mainColor;
				}
			}
		}
	}
}

body#safety {
	@include gPage;
	.myTitleBg {
		background-image:url(../img/csr/safety/img_bg.jpg);
	}
	
	.mySection01 {
		.myText {
			margin-bottom: 100px;
			@include tabletP {
				margin-bottom: 40px;
			}
		}
		.myName {
			display: grid;
			max-width: 200px;
			width: 100%;
			margin: 0 auto;
			@include tabletPMin {
				grid-template:
				"ttl name01" auto
				"ttl name02" auto /
				auto auto;
				grid-gap:0 15px;
			}
			@include tabletP {
				max-width: 170px;
				grid-template:
				"ttl name01" auto
				"name02 name02" auto /
				auto auto;
				grid-gap:0 15px;
			}
			
			.ttl {
				grid-area: ttl;
			}
			.name01 {
				grid-area: name01;
			}
			.name02 {
				grid-area: name02;
				@include tabletP {
					text-align: center;
				}
			}
		}
	}
	.mySection03 {
		.myList {
			max-width: 650px;
			margin-right: auto;
			margin-left: auto;
			@include tabletP {
				max-width: 70vw;
			}
		}
	}
	.mySection04 {
		.myText {
			margin-bottom: 90px;
			@include tabletP {
				margin-bottom: 50px;
			}
		}
		.myPhoto {
			text-align: center;
			margin: 0 auto;
			@include tabletP {
				max-width: 75vw;
			}
		}
	}
	.mySection05 {
		.myBlockTileBox {
			max-width: 750px;
			margin-right: auto;
			margin-left: auto;
			.list {
				display: flex;
				flex-wrap: wrap;
				margin: 0 -25px;
				@include tabletP {
					margin: 0 -7.5px;
				}
			}
			.item {
				width: 25%;
				padding-right: 25px;
				padding-left: 25px;
				@include tabletP {
					width: 50%;
					padding-right: 7.5px;
					padding-left: 7.5px;
					margin-bottom: 15px;
				}
			}
			.box {
				border: 1px solid $mainColor;
				text-align: center;
				height: 100%;
				padding: 20px 10px;
				color: $mainColor;
				@include tabletP {
					min-height: 115px;
					padding: 20px 10px;
				}
				.num {
					@include fz(32);
					font-weight: bold;
					line-height: 1;
					margin-bottom: 15px;
					@include tabletP {
						@include fz(24);
						margin-bottom: 10px;
					}
				}
			}
		}
	}
	
	.mySection06 {
		.myBlock {
			max-width: 300px;
			margin: 0 auto 80px;
			@include tabletP {
				max-width: 250px;
				margin-bottom: 40px;
			}
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	
	.mySection07 {
		.myBlock {
			text-align: center;
			margin-bottom: 80px;
			@include tabletP {
				margin-bottom: 60px;
			}
			&:last-child {
				margin-bottom: 0;
			}
		}
		.myTtl {
			margin-top: 50px;
			margin-bottom: 50px;
			@include tabletP {
				margin-top: 0;
				margin-bottom: 10px;
			}
		}
		.myList {
			>li {
				margin-bottom: 5px;
				&:last-child {
					margin-bottom: 0;
				}
				a {
					margin-left: 15px;
				}
			}
		}
	}
}

body#work {
	@include gPage;
	.myTitleBg {
		background-image:url(../img/csr/work/img_bg.jpg);
	}
	.myBlock {
		margin-bottom: 90px;
		@include tabletP {
			margin-bottom: 50px;
		}
		&:last-child {
			margin-bottom: 0;
		}
	}
	.myBlock01 {
		margin-bottom: 60px;
		&__header {
			margin-bottom: 25px;
			.vect {
				width: 80px;
				text-align: center;
				margin: 10px 0;
				@include tabletP {
					width: 70px;
					margin: 20px 0;
				}
			}
		}
	}
	.myBlock > .myBlock01:last-child {
		&:last-child {
			margin-bottom: 0;
		}
	}
	.myText {
		@include tabletP {
			margin-bottom: 100px;
		}
	}
	.myDlist {
		display: flex;
		align-items: center;
		@include fz(17);
		@include tabletP {
			display: table;
			table-layout: fixed;
			width: 100%;
			@include fz(12);
		}
		dt,dd{
			padding: 10px 15px;
			line-height: 1.2;
			border: 1px solid #000;
			@include tabletP {
				display: table-cell;
				vertical-align: middle;
				padding: 10px;
			}
		}
		dt {
			width: 80px;
			text-align: center;
			border-right: none;
			border-top-left-radius: 100px;
			border-bottom-left-radius: 100px;
			@include tabletP {
				width: 70px;
			}
		}
		dd {
			max-width: calc(100% - 80px);
			border-left: 1px solid #000;
			border-top-right-radius: 100px;
			border-bottom-right-radius: 100px;
			@include tabletP {
				width: auto;
			}
		}
		&.-blue {
			dt,dd {
				background: rgba($mainColor,.3);
			}
		}
	}
	.modOlist02 {
		margin-top: -10px;
		@include tabletP {
			padding-left: 20px;
		}
	}
	.modList01 {
		> li {
			.inner {
				width: calc(100% - 1em);
				display: inline-flex;
				text-indent: 0;
			}
			.day {
				width: 160px;
				@include tabletP {
					width: 110px;
				}
			}
			.txt {
				width: calc(100% - 160px);
				@include tabletP {
					width: calc(100% - 110px);
				}
			}
			.fulltxt {
				width: 100%;
			}
		}
	}
}