@charset "utf-8";

body#top {
	.myVisual {
		position: relative;
		height: 100vh;
		@include tabletL {
			height: auto !important;
			margin-bottom: 120px;
		}
		&Movie {
			position: fixed;
			top: 0;
			left: 0;
			width: 100%;
			height: 100vh;
			z-index: -1;
			@include tabletL {
				position: static;
				background-image: url(../img/top/img_visual_bg_sp.jpg);
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
			}
			iframe {
				position: absolute;
				vertical-align: top;
				top: 0;
				left: 0;
				min-width: 100%;
				min-height: 100vh;
				pointer-events: none;
				z-index: -1;
			}
		}
		&Navi {
			position: fixed;
			top: 50%;
			left: 0;
			width: 100%;
			transform: translateY(-50%);
			@include tabletL {
				position: static;
				transform: translateY(0);
				padding-top: 50px;
				padding-bottom: 50px;
			}
			.list {
				display: flex;
				flex-wrap: wrap;
				justify-content: center;
				&Item {
					width: 50%;
					text-align: center;
					&:last-child {
						@include tabletP {
							width: 60%;
						}
						.inner {
							padding-top:40px;
							@include tabletP {
								padding-top: 20px;
							}
						}
					}
					> a {
						display: block;
						color: #000;
					}
					.inner {
						position: relative;
						@include tabletL {
							padding-top: 40px;
						}
					}
					.text {
						width: 100%;
						@include fz(23);
						margin-bottom: 20px;
						@include tabletL {
							@include fz(12.5);
							text-shadow: none;
							top: 0;
							margin-bottom: 0;
						}
						> span {
							position: relative;
							display: inline-block;
							padding: 5px 20px;
							border: 1px solid #fff;
							background: #fff;
							border-radius: 100px;
							@include tabletL {
								padding: 5px 10px;
								border-color: #000;
							}
							&::before,
							&::after {
								content:" ";
								position: absolute;
								left: 50%;
								margin-left: -8px;
								@include tabletL {
									margin-left: -4px;
								}
							}
							&::before {
								bottom: -18px;
								width: 0;
								height: 0;
								border-style: solid;
								border-color: #fff transparent transparent transparent;
								border-width: 18px 6px 0 6px;
								@include tabletL {
									bottom: -12px;
									border-width: 12px 4px 0 4px;
									border-color: #000 transparent transparent transparent;
								}

							}
							&::after {
								bottom: -18px;
								width: 0;
								height: 0;
								border-style: solid;
								border-width: 18px 6px 0 6px;
								border-color: #fff transparent transparent transparent;
								@include tabletL {
									bottom: -10px;
									border-width: 12px 4px 0 4px;
									
								}
							}
						}
					}
				}
			}
		}
		&__icon {
			position: absolute;
			bottom: 30px;
			left: calc(50% - 20.5px);
			@include tabletP {
				bottom: 15px;
			}
		}
		&__target {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
		}
	}
	
	.myContentArea {
		position: relative;
		background: #fff;
		z-index: 0;
		padding-top: 170px;
		padding-bottom: 170px;
		@include tabletL {
			padding-top: 0;
			padding-bottom: 100px;
		}
	}
	
	.mySection01 {
		position: relative;
		z-index: 1;
		&__bgparts {
			position: absolute;
			&.-parts01 {
				top: -140px;
				left: 30px;
				@media screen and (max-width:1280px) {
					top: -100px;
					width: 200px;
				}
				@include tabletL {
					top: -110px;
					left: 15px;
					width: 140px;
				}
			}
			&.-parts02 {
				right: 30px;
				bottom: 90px;
				@include pc {
					width: 250px;
				}
				@include tabletL {
					right: 15px;
					bottom: 140px;
					width: 170px;
				}
			}
		}
		.modBlockNewsMain,
		.modBlockNewsSub {
			position: relative;
			z-index: 1;
		}
	}
	
	.mySection02 {
		position: relative;
		padding-top: 290px;
		padding-bottom: 200px;
		@include tabletP {
			padding-top: 200px;
			padding-bottom: 150px;
		}
		&__header {
			position: relative;
			z-index: 2;
		}
		&__ttl {
			@include fz(45);
			line-height: 1;
			text-align: center;
			font-weight: 500;
			margin-bottom: 45px;
			@include tabletP {
				@include fz(30);
			}
			.charWrap {
				padding: 0 .25em;
				@include tabletP {
					padding: 0 .2em;
				}
			}
			.hd {
				position: relative;
				display: inline-block;
				padding: .1em;
				.main {
					letter-spacing: -.05em;
					font-weight: bold;
				}
				.sub {
					position: absolute;
					right: -15px;
					top: 50%;
					transform: translateY(-50%);
					line-height: 1;
					@include tabletP {
						right: -10px;
					}
					> span {
						display: table;
						@include fz(12);
						line-height: 0;
						@include tabletP {
							@include fz(10);
						}
					}
					.vertical {
						margin-bottom: -.15em;
					}
				}
			}
		}
		&__button {
			text-align: right;
			margin-bottom: 75px;
		}
		&__navi {
			.list {
				display: flex;
				justify-content: center;
				flex-wrap: wrap;
				margin-right: -20px;
				margin-left: -20px;
				@include tabletP {
					justify-content: flex-start;
					margin-right: 0;
					margin-left: 0;
				}
			}
			.item {
				width: 33.333%;
				text-align: center;
				padding-right: 20px;
				padding-left: 20px;
				@include tabletP {
					width: 60%;
					padding-right: 0;
					padding-left: 0;
					&:nth-child(even) {
						margin-right: 0;
						margin-left: auto;
					}
				}
				a {
					position: relative;
					display: inline-block;
					vertical-align: middle;
					max-width: 100%;
					color: #fff;
					&::before {
						content: "";
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
						background: rgba(#000,.4);
						border-radius: 100vw;
						transition: .3s;
						opacity: 0;
					}
					img {
						vertical-align: middle;
					}
					.txt {
						position: absolute;
						top: 50%;
						left: 0;
						transform: translateY(-50%);
						width: 100%;
						z-index: 1;
						@include fz(24);
						line-height: 1.6;
						text-shadow: rgba(0,0,0,.5) -2px 2px 3px, rgba(0,0,0,.5) -2px 2px 3px;
						@include tabletL {
							@include fz(20)
						}
						@include sphoneP {
							font-size: 5vw;
						}
					}
					&:hover {
						@include tabletLMin {
							&::before {
								opacity: 1;
							}
						}
					}
				}
			}
		}
		&__bgparts {
			position: absolute;
			&.-parts01 {
				top: 40px;
				right: calc(50% - 40px);
				@include tabletL {
					top: 50px;
					width: 300px;
				}
				@include tabletP {
					top: 0;
					right: calc(50% - 20px);
					width: 220px;
				}
			}
			&.-parts02 {
				top: 300px;
				right: -40px ;
				@include tabletL {
					width: 400px;
				}
				@include tabletP {
					width: 280px;
				}
			}
		}
	}
	
	.mySection03 {
		position: relative;
		padding-top: 200px;
		padding-bottom: 100px;
		@include tabletP {
			padding-top: 100px;
			padding-bottom: 50px;
		}
		&__ttl {
			@include fz(45);
			line-height: 1;
			font-weight: 500;
			@include tabletP {
				@include fz(30);
			}
			.charWrap {
				padding: 0 .5em;
				@include tabletP {
					padding: 0 .4em;
				}
			}
			.char {
				text-align: center;
			}
			.vertical {
				margin-bottom: -.15em;
			}
		}
		&__bgparts {
			position: absolute;
			z-index: 1;
			&.-parts01 {
				top: -60px;
				left: 49%;
				@include tabletL {
					top: -100px;
					width: 300px;
				}
				@include tabletP {
					top: -80px;
					left: 43%;
					width: 200px;
				}
			}
			&.-parts02 {
				top: 100px;
				left: 30px;
				@include tabletL {
					width: 250px;
				}
				@include tabletP {
					top: 50px;
					left: 0;
					width: 180px;
				}
			}
			&.-parts03 {
				top: 200px;
				right: 30px;
				@include tabletL {
					top: 250px;
					width: 180px;
				}
				@include tabletP {
					top: 250px;
					right: 15px;
					width: 150px;
				}
			}
			&.-parts04 {
				bottom: -250px;
				right: 50%;
				@include tabletL {
					bottom: -150px;
					right: 45%;
					width: 450px;
				}
				@include tabletP {
					width: 50%;
					bottom: -120px;
				}
			}
		}
	}
	.mySection04 {
		position: relative;
		padding-top: 100px;
		padding-bottom: 30px;
		z-index: 1;
		&__ttl {
			> a,
			> span {
				position: relative;
				display: block;
				padding-top: 37%;
				overflow: hidden;
				@include tabletL {
					padding-top: 43%;
				}
				img {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%,-50%);
					max-width: 1280px;
					@include tabletL {
						max-width: 135vw;
					}
				}
			}
		}
		&__body {
			padding-top: 395px;
			@include tabletP {
				padding-top: 300px;
			}
		}
		&__box {
			display: flex;
			align-items: flex-end;
			max-width: 800px;
			margin:0 auto 20px;
			@include tabletP {
				display: block;
				margin-bottom: 80px;
			}
			.item {
				position: relative;
				z-index: 2;
				width: 50%;
				@include tabletP {
					width: 100%;
				}
				&.-text {
					position: relative;
					padding-bottom: 150px;
					@include tabletP {
						padding-bottom: 0;
						margin-bottom: 120px;
					}
					.modTxtVertical {
						position: relative;
						z-index: 1;
						@include fz(45);
						text-align: center;
						font-weight: 500;
						@include tabletP {
							width: 45%;
							@include fz(30);
							margin-left: auto;
							margin-right: 0;
						}
						.charWrap {
							padding: 0 .3em;
						}
						.char {
							line-height: 1;
						}
					}
					.bg {
						position: absolute;
						bottom: 0;
						left: -100px;
						@include tabletP {
							bottom: -520px;
							left: -15px;
							width: 42%;
						}
					}
				}
				&.-photo {
					text-align: center;
					@include tabletP {
						padding-bottom: 0;
					}
					a {
						position: relative;
						display: block;
						width: 300px;
						height: 300px;
						overflow: hidden;
						border-radius: 100vw;
						box-shadow:0 0 5px 6px rgba(#000,.2);
						margin: 0 0 0 auto;
						@include tabletL {
							width: 250px;
							height: 250px;
							margin: 0 auto;
						}
						&::before {
							content: '';
							position: absolute;
							top: 0;
							left: 0;
							width: 100%;
							height: 100%;
							background: rgba(0,0,0,.4);
							border-radius: 100vw;
							-webkit-transition: .3s;
							transition: .3s;
							opacity: 0;
						}
						&:hover {
							&::before {
								opacity: 1;
							}
						}
					}
					img {
						position: absolute;
						top: 38%;
						left: 50%;
						transform: translate(-50%,-50%);
						max-width: 730px;
						@include tabletP {
							top: 36%;
							max-width: 300px;
						}
					}
				}
			}
		}
		&__navi {
			position: relative;
			z-index: 3;
			.list {
				display: flex;
				flex-wrap: wrap;
				justify-content: center;
				margin-right: -20px;
				margin-left: -20px;
				@include tabletP {
					justify-content: flex-start;
					margin-right: 0;
					margin-left: 0;
				}
				.item {
					width: 33.333%;
					padding-right: 20px;
					padding-left: 20px;
					@include tabletP {
						width: 60%;
						padding-right: 0;
						padding-left: 0;
						&:nth-child(odd) {
							margin-right: 0;
							margin-left: auto;
						}
					}
					a {
						position: relative;
						display: inline-block;
						vertical-align: middle;
						max-width: 100%;
						&::before {
							content: "";
							position: absolute;
							top: 0;
							left: 0;
							width: 100%;
							height: 100%;
							background: rgba(#000,.4);
							border-radius: 100vw;
							transition: .3s;
							opacity: 0;
						}
						.txt {
							position: absolute;
							top: 50%;
							left: 0;
							transform: translateY(-50%);
							width: 100%;
							text-align: center;
							> span {
								display: block;
								margin-bottom: 5px;
								transition: .3s;
								img {
									vertical-align: middle;
								}
							}
						}
						&:hover {
							@include tabletLMin {
								&::before {
									opacity: 1;
								}
							}
						}
					}
					&.-item01 {
						a {
							.txt {
								> span {
									&:nth-child(1) {
										img {
											@include sphoneP {
												max-width: 25vw;
											}
										}
									}
									&:nth-child(2) {
										img {
											@include sphoneP {
												max-width: 20vw;
											}
										}
									}
								}
							}
							&:hover {
								.txt {
									> span {
										&:nth-child(1) {
											opacity: 0.4;
										}
									}
								}
							}
						}
					}
					&.-item02 {
						a {
							.txt {
								img {
									@include sphoneP {
										max-width: 43vw;
									}
								}
							}
						}
					}
				}
			}
		}
		
		&__bgparts {
			position: absolute;
			&.-parts01 {
				top: 600px;
				left: 8vw;
				@include tabletP {
					top: 390px;
					left: 15px;
					width: 70vw;
				}
			}
			&.-parts02 {
				top: 850px;
				right: 0;
				@include tabletP {
					top: 945px;
					right: 15px;
					width: 55vw;
				}
			}
		}
	}
}