.modOlist01 {
	counter-reset: olist01;
	margin-bottom: 60px;
	@include tabletP {
		margin-bottom: 40px;
	}
	> li {
		position: relative;
		counter-increment: olist01;
		padding-left: 1.5em;
		margin-bottom: 50px;
		@include tabletL {
			margin-bottom: 30px;
		}
		&::before {
			content: counter(olist01)'.';
			position: absolute;
			left: 0;
		}
	}
	&.-mbSmall {
		>li {
			margin-bottom: 10px;
		}
	}
}

.modOlist02 {
	>li {
		position: relative;
		margin-bottom: 5px;
		padding-left: 1.2em;
		&:last-child {
			margin-bottom: 0;
		}
		>.num {
			position: absolute;
			left: 0;
		}
	}
}