.modBtn01 {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	min-width: 160px;
	text-decoration: none;
	@include fz(16);
	overflow: hidden;
	padding:2px 30px 2px 0;
	letter-spacing: .1em;
	text-align: left;
	color: $baseColor;
	@include tabletL {
		@include fz(14);
		min-width: 120px;
	}
	&::before {
		content: " ";
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 1px;
		animation: line-out 1.2s 0s cubic-bezier(0.19, 0.865, 0.295, 1) both;
		background: #231815;
	}
	&::after {
		content:"→";
		position: absolute;
		@include fz(16);
		top: 50%;
		right: 5px;
		transform: translateY(-50%);
		transition: right .5s;
	}
	&[aria-hidden="false"]:not(:hover) {
		&::before,
		&::after {
			opacity: 1;
		}
	}
	&[aria-hidden="true"] {
		&::before,
		&::after {
			opacity: 0;
		}
	}
	&:hover {
		@include tabletLMin {
			opacity: 1;
			&::before {
				animation : line 1.2s 0s cubic-bezier(0.19, 0.865, 0.295, 1)both
			}
			&::after {
				right: 0;
			}
		}
	}
	&.-small {
		@include fz(14);
		min-width: 160px;
		&::after {
			right: 5px;
			width: 6px;
			background-size: 6px;
		}
		&:hover {
			@include tabletLMin {
				&::after {
					right: 0;
				}
			}
		}
	}
	&.-large {
		min-width: 330px;
		letter-spacing: 0;
		@include tabletP {
			min-width: 200px;
		}
	}
	&.-noicon {
		padding-right: 0;
		min-width: auto;
		&::after {
			display: none;
		}
		@include tabletP {
			@include fz(12);
		}
	}
	&.-blue {
		color: $mainColor;
		&::before {
			background: $mainColor;
		}
	}
}

@keyframes line {
	100% {
		transform: translateX(100%)
	}
}
@keyframes line-out {
	0% {
		transform: translateX(-100%)
	}
	100% {
		transform: translateX(0%)
	}
}