.modBLock01 {
	margin-bottom: 80px;
	@include tabletP {
		margin-bottom: 50px;
	}
	&:last-child {
		margin-bottom: 0;
	}
}

.modBlockNews {
	&Main {
		border: 1px solid #231815;
		border-radius: 13px;
		padding: 0 40px 50px;
		margin-bottom: 80px;
		@include tabletL {
			padding: 0 15px 15px;
			margin-bottom: 80px;
		}
		&:last-child {
			margin-bottom: 0;
		}
		&__header {
			display: flex;
			justify-content: space-between;
			align-items: center;
			transform: translateY(-60%);
			padding: 0 40px;
			@include tabletL {
				display: block;
				transform: translateY(-35%);
				padding: 0;
			}
			&Ttl {
				line-height: 1;
				font-weight: 500;
				@include fz(40);
				background: #fff;
				padding: 0 10px;
				@include tabletL {
					@include fz(30);
					display: table;
					margin:0 auto 10px;
				}
			}
			&Btn {
				display: flex;
				flex-wrap: wrap;
				justify-content: center;
				align-items: center;
				background: #fff;
				line-height: 1;
				@include tabletL {
					background: none;
					text-align: center;
				}
				> li {
					border-right:1px solid $baseColor;
					line-height: 1.2;
					@include tabletL {
						line-height: 1;
					}
					&:last-child {
						border-right:none;
					}
					>a,
					>span {
						text-decoration: none;
						@include fz(17);
						font-family: $fontGothic;
						color:$baseColor;
						padding: 0 15px;
						@include tabletL {
							@include fz(11);
						}
					}
					>a {
						&:hover {
							@include tabletLMin {
								text-decoration: underline;
							}
						}
					}
				}
			}
		}
		&__body {
			padding-top: 20px;
			@include tabletL {
				padding-top: 10px;
			}
		}
	}
}

.modBlockBox01 {
	background: #B2CBE4;
	display: flex;
	align-items: center;
	justify-content:space-between;
	border-radius: 13px;
	padding: 10px 40px;
	@include fz(16);
	@include tabletL {
		display: block;
		padding: 20px 10px 10px;
	}
	&__text {
		width: calc(100% - 160px);
		@include tabletL {
			width: 100%;
			text-align: center;
			@include fz(14);
			margin-bottom: 15px;
		}
		p {
			margin-bottom: 5px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	&__button {
		width: 160px;
		@include tabletL {
			width: 100%;
			text-align: right;
		}
	}
}

.modBlockEllipse {
	&::after {
		content: "";
		display: table;
		clear: both;
	}
	&__outer {
		position: relative;
		left: 50%;
		transform: translateX(-50%);
		border-radius: 200% 200% ;
		overflow: hidden;
		width: 1350px;
		float: left;
		@include tabletL {
			width: 110%;
		}
	}
	&__inner {
		display: flex;
		justify-content: center;
		align-items: center;
		background: #eee;
		height: 570px;
		background-repeat: no-repeat;
		background-size: cover;
		// transition: background-position-y .3s;
		@include tabletL {
			height: 42vw;
		}
	}
	&__title {
		@include fz(50);
		color: #fff;
		text-shadow: 1px 1px 2px rgba(0,0,0,.7);
		@include tabletL {
			font-size: 6vw;
		}
	}
}

.modBlockSection {
	margin-bottom: 100px;
	@include tabletP {
		margin-bottom: 50px;
	}
	&:last-child {
		margin-bottom: 0;
	}
	&__body {
		padding-top: 100px;
		padding-bottom: 100px;
		@include tabletL {
			padding-top: 50px;
			padding-bottom: 50px;
		}
		&:first-child {
			padding-top: 0;
			@include tabletP {
				padding-bottom: 0;
			}
		}
	}
	&.-first {
		.modBlockSection__header {
			.modBlockEllipse {
				margin-top: -70px;
				@include tabletL {
					margin-top: 0;
				}
				&__inner {
					padding-top: 70px;
					@include tabletL {
						padding-top: 0;
					}
				}
			}
		}
	}
}

.modBlockTblWrap {
	@include tabletL {
		overflow: auto;
		>table {
			min-width: 1000px;
		}
	}
}

.modBlockFixedNav {
	position: fixed;
	top: 80px;
	right: 15px;
	z-index: 10;
	@include tabletP {
		top: auto;
		right:auto;
		bottom: 0;
		left:0;
		width: 100%;
		border-top: 1px solid $mainColor;
	}
	ul {
		@include tabletP {
			display: flex;
		}
	}
	li {
		margin-bottom: 15px;
		@include tabletP {
			width: 25%;
			margin-bottom: 0;
		}
		&:last-child {
			margin-bottom: 0;
		}
		a {
			display: flex;
			width: 120px;
			height: 58px;
			justify-content: center;
			align-items: center;
			border: 1px solid $mainColor;
			background: #fff;
			border-radius: 100%;
			line-height: 1.5;
			text-decoration: none;
			@include fz(12);
			transition: .3s;
			box-shadow: 0 0 2px 1px rgba(#fff,.5);
			@include tabletP {
				position: relative;
				width: 100%;
				height: 50px;
				border-radius: 0;
				box-shadow: none;
				border: none;
				background:rgba(#fff,.8);
				&::after {
					content: " ";
					position: absolute;
					top: 5px;
					bottom: 5px;
					left: 0;
					width: 1px;
					height: calc(100% - 10px);
					background: $mainColor;
					transition: .3s;
				}
			}
			&:hover {
				@include tabletLMin {
					background: $mainColor;
					color:#fff;
				}
			}
			&[aria-selected="true"]{
				background: $mainColor;
				color:#fff;
			}
		}
		&.-active + li,
		&:first-child {
			a {
				&::after {
					display: none;
				}
			}
		}
	}
}

.modBlockFormBtn {
	text-align: center;
	input[type=submit] {
		border: none;
		@include fz(17);
		border-radius: 9px;
		background: rgba($mainColor,.3);
		height: 40px;
		padding: 0 15px;
		margin: 0 15px;
		min-width: 100px;
		appearance: none;
	}
}

.modBlockSmallWrap {
	max-width: 600px;
	margin: 0 auto;
}

.modBlockScrollArrow {
	text-align: right;
	display: none;
	transition: opacity .3s;
	@include tabletL {
		display: block;
	}
	img {
		width: 65px;
	}
}

.modScrollBlock {
	position: relative;
	.modBlockScrollArrow {
		position: absolute;
		right: -7px;
		top: 0;
		transform: translateY(-80%);
	}
}