@charset "utf-8";

body#about {
	.myTitleBg {
		&01 {
			background-image:url(../img/about/img_bg_01.jpg);
		}
		&02 {
			background-image:url(../img/about/img_bg_02.jpg);
		}
	}
	
	.mySection01 {
		margin-bottom: -380px;
		@include tabletL {
			margin-bottom: -20vw;
		}
		@include tabletP {
			margin-bottom: -25vw;
		}
		&_1 {
			text-align: center;
			margin-bottom: -430px;
			@include tabletL {
				margin-bottom: -300px;
			}
			@include sphoneP {
				margin-bottom: -100px;
			}
			&::after {
				content: "";
				display: table;
				clear: both;
			}
			.outer {
				position: relative;
				display: flex;
				justify-content: center;
				align-items: center;
				left: 50%;
				transform: translateX(-50%);
				float: left;
				width: 1720px;
				height: 850px;
				background: $mainColor;
				color: #fff;
				border-radius: 100%;
				@include tabletL {
					width: 180vw;
					height: 180vw;
				}
				@include tabletP {
					width: 115vw;
					height: 115vw;
				}
			}
			.container {
				margin: 0;
				@include tabletP {
					width: 100vw;
				}
			}
			.title {
				@include fz(50);
				margin-bottom: 100px;
				@include tabletL {
					@include fz(36);
				}
				@include tabletP {
					font-size: 5vw;
					margin-bottom: 10vw;
					line-height: 1.8;
				}
			}
			.text {
				@include fz(36);
				@include tabletL {
					@include fz(28)
				}
				@include tabletP {
					font-size: 3.5vw;
					line-height: 2.2;
				}
			}
		}
		&_2 {
			background: #f6f6f6;
			padding-top: 550px;
			padding-bottom: 400px;
			@include tabletL {
				padding-bottom: calc(22.5vw + 70px);
			}
			@include tabletP {
				padding-top: calc(45.5vw + 70px);
				padding-bottom: 25vw;
			}
			.myDlist {
				max-width: 700px;
				margin: 0 auto;
				@include fz(12);
				text-align: left;
				&__item {
					display: flex;
					margin-bottom: 15px;
					@include tabletP {
						display: block;
						margin-bottom: 10px;
					}
					&:last-child {
						margin-bottom: 0;
					}
				}
				&__header {
					width: 120px;
					@include tabletP {
						width: 100%;
						font-weight: 700;
					}
				}
				&__body {
					width: calc(100% - 120px);
					@include tabletP {
						width: 100%;
					}
				}
			}
			.myDlistIn {
				&__item {
					display: flex;
					margin-bottom: 15px;
					@include tabletP {
						margin-bottom: 0;
					}
					&:last-child {
						margin-bottom: 0;
					}
				}
				&__header {
					width: 135px;
					@include tabletP {
						width: 125px;
					}
				}
				&__body {
					width: calc(100% - 135px);
					@include tabletP {
						width: calc(100% - 125px);
					}
					.name,
					.pos {
						@include tabletP {
							display: block;
						}
					}
				}
			}
		}
	}
	
	.mySection02 {
		.modBlockSection__body {
			@include tabletP {
				padding-bottom:30px;
			}
		}
		&_1 {
			text-align: center;
			margin-bottom: 120px;
			@include tabletL {
				margin-bottom: 60px;
			}
			.main {
				@include fz(70);
				line-height: 1;
				margin-bottom: 10px;
				@include tabletL {
					@include fz(45)
				}
				.rt {
					display: block;
					@include fz(12);
					text-align: center;
					letter-spacing: .6em;
					margin-bottom: 5px;
					@include tabletL {
						@include fz(10);
					}
				}
				.rb {
					display: block;
					font-weight: 600;
					padding-right: 0.1em;
				}
			}
			.sub {
				@include fz(18);
				@include tabletL {
					@include fz(11);
				}
			}
		}
		&_2 {
			text-align: center;
			.list {
				display: inline-block;
				text-align: left;
				@include fz(36);
				font-weight: 600;
				margin-bottom: 100px;
				@include tabletL {
					@include fz(25);
					margin-bottom: 70px;
				}
			}
			.desc {
				margin-bottom: 80px;
				@include tabletL {
					@include fz(12);
					margin-bottom: 70px;
				}
			}
			.column {
				display: flex;
				flex-wrap: wrap;
				margin: 0 -25px;
				.item {
					width: 50%;
					padding-right: 25px;
					padding-left: 25px;
					margin-bottom: 50px;
					@include tabletP {
						width: 100%;
						margin-bottom: 30px;
					}
					&.-fullsize {
						width: 100%;
						&:last-child {
							margin-bottom: 0;
						}
					}
					.inner {
						background: #f6f6f6;
						border-radius: 13px;
						padding: 40px;
						height: 100%;
						@include tabletL {
							padding: 20px;
						}
					}
					.title {
						color: $mainColor;
						@include fz(22);
						font-weight: 600;
						line-height: 1.2;
						margin-bottom: 30px;
						@include tabletL {
							@include fz(14);
							margin-bottom: 15px;
						}
					}
					.text {
						text-align: left;
						line-height: 1.6;
						@include tabletL {
							@include fz(11);
						}
					}
					
					.boxText {
						background: #fff;
						line-height: 1.2;
						border-radius: 100px;
						padding: 10px;
						margin-bottom: 15px;
						display: flex;
						justify-content: center;
						font-size: 18px;
						@include tabletL {
							@include fz(12);
						}
						@include tabletP {
							border-radius: 12px;
						}
						dt {
							white-space: nowrap;
						}
						dd {
							text-align: left;
							>span {
								color: $mainColor;
							}
						}
					}
				}
			}
		}
	}
	
	.mySection03 {
		background: #f6f6f6;
		padding-top: 200px;
		padding-bottom: 200px;
		@include tabletL {
			padding-top: 80px;
			padding-bottom: 80px;
		}
		.modBlockSection__body {
			padding-top: 0;
			padding-bottom: 0;
		}
		&__inBlock {
			margin-bottom: 150px;
			@include tabletP {
				margin-bottom: 80px;
			}
			&:last-child {
				margin-bottom: 0;
			}
			a {
				text-decoration: none;
				color: $baseColor;
			}
		}
		.text {
			text-align: center;
			margin-bottom: 70px;
			@include tabletP {
				@include fz(12);
				line-height: 2;
				margin-bottom: 50px;
			}
		}
		.modTable01 {
			text-align: left;
		}
		
	}
	.mySection04 {
		padding-top: 100px;
		padding-bottom: 100px;
		@include tabletL {
			padding-top: 30px;
			padding-bottom: 40px;
		}
		.modBlockSection__body {
			padding-top: 0;
		}
		.list {
			max-width: 750px;
			margin: 0 auto 90px;
			&:last-child {
				margin-bottom: 0;
			}
			@include tabletP {
				margin-bottom: 50px;
			}
			.modDlist02__header {
				width: 170px;
				@include tabletP {
					width: 140px;
				}
			}
			.modDlist02__body {
				width: calc(100% - 170px);
				@include tabletP {
					width: calc(100% - 140px);
				}
			}
		}
		.bannerBox {
			text-align: center;
			@include tabletP {
				@include fz(10);
			}
			.banner {
				margin-bottom: 40px;
				@include tabletP {
					margin-bottom: 20px;
				}
				img {
					@include tabletP {
						max-width: 180px;
					}
				}
			}
		}
	}
}