.modDlist01 {
	&__item {
		display: flex;
		align-items: stretch;
		@include tabletP {
			@include fz(12);
		}
	}
	&__item:first-child {
		.modDlist01__header,
		.modDlist01__body {
			padding-top: 0;
		}
	}
	&__item:last-child {
		.modDlist01__header,
		.modDlist01__body {
			padding-bottom: 0;
		}
	}
	&__header,
	&__body {
		padding: 7.5px 30px;
		@include tabletP {
			padding: 5px 10px;
		}
	}
	&__header {
		width: 120px;
		border-right: 1px solid #000;
		@include tabletP {
			width: 70px;
		}
	}
	&__body {
		width: calc(100% - 120px);
		@include tabletP {
			width: calc(100% - 70px);
		}
		>p {
			margin-bottom: 10px;
			@include tabletP {
				margin-bottom: 0;
			}
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}

.modDlist02 {
	&__item {
		display: flex;
		align-items: stretch;
		@include fz(12);
		@include tabletP {
			@include fz(11);
			margin-right: -10px;
			margin-left: -10px;
		}
	}
	&__item:first-child {
		.modDlist02__header {
			&::after {
				top: 50%;
				height: 50%;
				@include tabletP {
					top: 15px;
					height: 100%;
				}
			}
		}
	}
	&__item:last-child {
		.modDlist02__header {
			&::after {
				height: 50%;
				@include tabletP {
					top: -7px;
				}
			}
		}
	}
	&__header,
	&__body {
		padding:10px 20px;
		@include tabletP {
			padding:5px 10px;
		}
	}
	&__header {
		position: relative;
		width: 185px;
		@include tabletP {
			width: 140px;
		}
		&::after {
			content: "";
			position: absolute;
			top: 0;
			right: 0;
			width: 1px;
			height: 100%;
			background: #000;
		}
		.inner {
			position: relative;
			display: flex;
			justify-content: space-between;
			width: 100%;
			.main {
				&::after {
					content:"";
					position: absolute;
					top: 6px;
					right: -25px;
					width: 10px;
					height: 10px;
					background: #000;
					border-radius: 10px;
					@include tabletP {
						right: -13px;
						width: 7px;
						height: 7px;
					}
				}
			}
			.sub {
				width: 25px;
			}
		}
	}
	&__body {
		width: calc(100% - 190px);
		@include tabletP {
			width: calc(100% - 140px);
		}
	}
}


.modDlistForm {
	max-width: 800px;
	margin: 0 auto 80px;
	@include tabletP {
		margin-bottom: 40px;
	}
	&__item {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin-bottom: 40px;
		@include tabletP {
			margin-bottom: 15px;
		}
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__header {
		width: 190px;
		@include tabletP {
			width: 100%;
			margin-bottom: 5px;
		}
	}
	&__body {
		width: calc(100% - 190px);
		@include tabletP {
			width: 100%;
		}
		input:not([type=checkbox]):not([type=radio]),textarea {
			width: 100%;
			vertical-align: middle;
		}
		.error {
			color: #ff0000;
			display: block;
			margin-top: 5px;
		}
	}
}