@charset "utf-8";

/* wysiwyg
------------------------------------------------------------*/
.modParts{
	margin-bottom:20px;
}

.modPartsImage{
	text-align:center;
	color:#959595;
	@include fz(11);
	.caption{
		margin:5px 0 0;
	}
	a:hover{
		opacity:0.8;
	}
}

.modPartsYoutube {
	max-width:560px;
	margin-left:auto;
	margin-right:auto;

	@include tabletP{
		position: relative;
		width: 100%;
		padding-top: 56.25%;
		iframe {
		  position: absolute;
		  top: 0;
		  right: 0;
		  width: 100% !important;
		  height: 100% !important;
		}
	
	}
}

.modPartsTxt{
	iframe{
		width:100%;
		height:400px;
		@include tabletP{
			height:250px;
		}
	}

	ul {
		margin-left: 2em;
	
		li {
			list-style: disc;
			
		}
	}
	ol {
		margin-left: 2.2em;
	li {
		list-style: decimal;
		
	}
	}
	strong,
	th {
		font-weight:bold;
	}
	
	.aligncenter {
		display: block;
		margin-left: auto;
		margin-right: auto;
	}
	img.alignright {
		padding: 4px;
		margin: 0 0 2px 7px;
		display: inline;
	}
	img.alignleft {
		padding: 4px;
		margin: 0 7px 2px 0;
		display: inline;
	}
	.alignright {
		float: right;
	}
	.alignleft {
		float: left;
	}
	strong{
		font-weight:bold;
	}

}
