@charset "utf-8";

body#contact,
body#conf,
body#comp {
	.myBlockTel {
		text-align: center;
		margin-top: 100px;
		@include tabletP {
			margin-top: 50px;
		}
		&:first-child {
			margin-top: 0;
		}
		.bold {
			@include fz(28);
			@include tabletP {
				@include fz(18);
				margin-top: 10px;
				&:first-child {
					margin-top: 0;
				}
			}
		}
	}
	.myBlockComp {
		text-align: center;
		> p {
			margin-bottom: 20px;
		}
		.btn {
			margin-top: 180px;
			@include tabletP {
				margin-top: 80px;
			}
		}
	}
}