.modTable01 {
	width: 100%;
	table-layout: fixed;
	@include fz(12);
	@include tabletP {
		@include fz(11);
	}
	th,td {
		padding:7.5px 15px;
		vertical-align: top;
		line-height: 1.6;
		@include tabletP {
			padding:7.5px 15px;
		}
	}
	thead {
		th {
			color:$mainColor;
		}
	}
	tbody {
		background: #fff;
		th,td {
			background: #fff;
		}
		tr {
			&:nth-child(odd) {
				th,td {
					background: rgba($mainColor,0.05);
				}
			}
		}
	}
	&.-bgGray {
		tbody {
			tr {
				th,td {
					background: #fff;
				}
				&:nth-child(even) {
					th,td {
						background: #f6f6f6;
					}
				}
			}
		}
	}
}