.modListNormal { margin: 0 0 0 2em; }

ul.modListNormal {
	li{
		&:before {
			content: "\0030fb";
			margin-left:-2em;
			display: inline-block;
			width:2em;
			text-align:center;
		}
	}
}

ol.modListNormal li { list-style:decimal; }

/* indent */
.modIndent01 li {
	text-indent: -1em;
	margin: 0 0 0 1em;
}

.modIndent02 li {
	text-indent: -2.5em;
	margin: 0 0 0 2.5em;
}

/* フォーム
***************************************/
.modDlForm{
	margin:0 0 20px;
	
	dt{
		float:left;
		clear:both;
		width:16em;
		padding:33px 0 20px 63px;
		position:relative;
		
		@include tabletP{
			float:none;
			width:auto;
			padding:33px 0 0 63px;
		}
		&.required:before,
		&.any:before{
			position:absolute;
			left:0;
			top:33px;
			color:#FFF;
			@include fz(11);
			padding:2px 10px;
		}
		&.required:before{
			
			content:"必須";
			background:$colorStrong;
			
		}
		&.any:before{
			background:#999999;
			content:"任意";
		}
	}
	dd{
		border-bottom:solid 1px #ccc;
		padding:10px 0;
		padding-left:17em;
		
		@include tabletP{
			padding:10px 0;
		}
		.parts{
			padding:10px 0;
			textarea,
			input[type="text"],
			input[type="email"]
			{
				width:100%;
				
			}
			&.radio_inline,
			&.check_inline{
				>div{
					display:inline-block;
				}
			}
			&.name{
				display:flex;
				justify-content:space-between;
				>div{
					width:48.5%;
				}
				
			}
			&.post,&.tel{
				display:flex;
				.hyphen{
					padding:10px 10px 0 10px;
					
					@include sphoneP{
						padding:10px 5px 0 5px;
					}
				}
			}
			&.password{
				input{
					width:100%;
					max-width:300px;
					@include sphoneP{
						max-width:none;
					}
				}
				.text{
					padding:10px 0;
				}
			}
		}
	}
	
	.validationError{
		color:$colorStrong;
	}
}

.modListNews {
	.item {
		font-family: $fontGothic;
		margin-bottom: 17px;
		@include tabletL {
			border-bottom: 1px solid $baseColor;
			margin-bottom: 0;
		}
		&:last-child {
			margin-bottom: 0;
			@include tabletL {
				border-bottom: none;
			}
		}
		>a {
			display: flex;
			align-items: flex-start;
			text-decoration: none;
			color: $baseColor;
			@include tabletL {
				flex-wrap: wrap;
				@include fz(10);
				padding: 10px 0;
			}
			.cat {
				background: #00213a;
				color: #fff;
				width: 135px;
				text-align: center;
				@include tabletL {
					width: 100px;
				}
			}
			.date {
				width: 170px;
				padding: 0 20px;
				@include tabletL {
					width: 120px;
					padding: 0 0 0 10px;
					@include fz(11);
				}
			}
			.name {
				width: calc(100% - 255px);
				@include tabletL {
					width: 100%;
					@include fz(11);
					padding-top: 7.5px;
				}
			}
			&:hover {
				@include tabletLMin {
					.name {
						text-decoration: underline;
					}
				}
			}
		}
	}
}

.modList01 {
	>li {
		padding-left: 1em;
		text-indent: -1em;
		margin-bottom: 5px;
		&:last-child {
			margin-bottom: 0;
		}
		&::before {
			content: "・";
		}
	}
}

.modListTile {
	display: flex;
	flex-wrap: wrap;
	margin-right: -10px;
	margin-left: -10px;
	@include tabletP {
		margin-right: -7.5px;
		margin-left: -7.5px;
	}
	> li {
		width: 33.333%;
		padding-right: 10px;
		padding-left: 10px;
		margin-bottom: 50px;
		text-align: center;
		@include fz(20);
		line-height: 1.2;
		@include tabletL {
			@include fz(16);
			margin-bottom: 30px;
		}
		@include tabletP {
			@include fz(12);
			width: 50%;
			padding-right: 7.5px;
			padding-left: 7.5px;
		}
		> a,
		> span {
			display: block;
			text-decoration: none;
			color: $baseColor;
			.photo {
				position: relative;
				display: inline-block;
				max-width: 100%;
				vertical-align: middle;
				margin-bottom: 10px;
				&::after {
					content:" ";
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background: rgba(#000,.4);
					transition: .3s;
					opacity: 0;
				}
			}
		}
		a {
			&:hover {
				@include tabletLMin {
					.photo::after {
						opacity: 1;
					}
				}
			}
		}
	}
}

.modListIndent01 {
	padding-left: 1em;
	text-indent: -1em
}
.modListIndent02 {
	padding-left: 2em;
	text-indent: -2em
}
.modListIndent03 {
	padding-left: 3em;
	text-indent: -3em
}


.modListBtn {
	text-align: center;
	> li {
		display: inline-block;
		margin: 5px;
	}
}