body {
	&.-noscroll {
		position: fixed;
		left: 0;
		width: 100%;
		height: 100vh;
	}
}
#wrapper {
	overflow: hidden;
}

.gHead {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	background:rgba(#fff,.9);
	transition: transform .3s;
	z-index: 11;
	&.-top {
		transform: translateY(-100%);
		@include tabletL {
			transform: translateY(0);
		}
		&.-visible {
			transform: translateY(0);
		}
	}
	&[aria-expanded="true"] {
		@include tabletL {
			height: 100%;
		}
	}
	&__inner {
		display: flex;
		align-items: center;
	}
	
	&__logo {
		position: relative;
		width: 215px;
		text-align: center;
		z-index: 2;
		@include tabletL {
			padding-left: 15px;
			width: 163px;
		}
		a {
			display: block;
			@include tabletLMin {
				transition: .3s;
				&:hover {
					opacity: .65;
				}
			}
			@include tabletL {
				height: 50px;
				display: flex;
				align-items: center;
				padding-bottom: 5px;
			}
			img {
				vertical-align: middle;
				width:180px;
				@include tabletL {
					width: 163px;
				}
			}
		}
	}
	&__navi {
		width: calc(100% - 215px);
	}
}
.gNavToggle {
	position: absolute;
	top: 5px;
	right: 10px;
	z-index: 2;
	@include tabletLMin {
		display: none;
	}
	a {
		display: block;
		width: 40px;
		height: 40px;
	}
	a::before,
	a::after,
	.bar {
		position: absolute;
		left: 5px;
		width: 30px;
		height: 2px;
		background: $baseLinkColor;
		transition: .3s;
	}
	.bar {
		top:calc(50% - 1px)
	}
	a::before,
	a::after {
		content: "";
	}
	a::before {
		top: calc(50% - 11px);
	}
	a::after {
		top: calc(50% + 9px);
	}
	a[aria-expanded=true]{
		.bar {
			left: 20px;
			opacity: 0;
		}
		&::before {
			top:calc(50% - 1px);
			transform: rotate(45deg);
		}
		&::after {
			top:calc(50% - 1px);
			transform: rotate(-45deg);
		}
	}
}
.gNav {
	@include tabletL {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: #fff;
		padding: 50px 15px;
		overflow: hidden;
		display: none;
	}
	&__inner {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		@include tabletL {
			display: block;
			border-top: 1px solid $baseLinkColor;
			height: 100%;
			overflow:auto;
		}
	}
	&__list {
		width: calc(100% - 100px);
		@include tabletL {
			width: 100%;
		}
		&Inner {
			display: table;
			margin: 0 0 0 auto;
			@include tabletL {
				display: block;
			}
		}
		&Item {
			position: relative;
			display: table-cell;
			vertical-align: middle;
			@include fz(14);
			@include pc {
				@include fz(12);
			}
			@include tabletL {
				display: block;
				border-bottom: 1px solid $mainColor;
				padding: 14px;
			}
			> a,
			> span {
				display: flex;
				align-items: center;
				justify-content: center;
				text-decoration: none;
				padding:0 15px;
				height: 60px;
				color:$baseLinkColor;
				@include tabletLMin {
					transition: .3s;
					&:hover{
						background: $baseLinkColor;
						color: #fff;
					}
					&[aria-current="page"] {
						background: $baseLinkColor;
						color: #fff;
					}
				}
				@include pc {
					padding:0 10px;
				}
				@include tabletL {
					height: auto;
					display: block;
					text-align: left;
					@include fz(14);
					color:$baseColor;
					padding: 0;
					&[target="_blank"] {
						position: relative;
						padding-right:15px;
						&::after {
							content: "";
							position: relative;
							top: -1px;
							display: inline-block;
							margin-left: 5px;
							width: 10px;
							height: 10px;
							background: url(../img/common/icon_newwindow_b.svg) no-repeat center;
							background-size: 10px 10px;
							opacity: 0.3;
						}
					}
				}
				.inner {
					display: inline-block;
					vertical-align: middle;
				}
			}
			> ul {
				position: absolute;
				left: 0;
				background: rgba(#fff,.6);
				@include tabletL {
					position: static;
					padding-top: 5px;
				}
				li {
					position: relative;
					@include tabletL {
						margin-bottom: 5px;
						&:last-child {
							margin-bottom: 0;
						}
					}
					&::before {
						content:" ";
						position: absolute;
						top: -1px;
						left: 5px;
						width: calc(100% - 10px);
						height: 1px;
						background: $mainColor;
						@include tabletL {
							display: none;
						}
					}
					&:first-child {
						&::before {
							display: none;
						}
					}
					a {
						display: block;
						white-space: nowrap;
						padding:10px 15px 11px;
						text-decoration: none;
						@include tabletL {
							padding: 0;
							@include fz(12);
							color: $baseColor;
							&[target="_blank"] {
								position: relative;
								padding-right:15px;
								&::after {
									content: "";
									position: relative;
									display: inline-block;
									margin-left: 5px;
									width: 10px;
									height: 10px;
									background: url(../img/common/icon_newwindow_b.svg) no-repeat center;
									background-size: 10px 10px;
									opacity: 0.3;
								}
							}
						}
					}
					@include tabletLMin {
						a {
							transition: .3s;
						}
						&:hover {
							&::before {
								display: none;
							}
							a {
								background: $mainColor;
								color: #fff;
							}
						}
					}
				}
			}
			&:last-child {
				>ul {
					left: auto;
					right:0;
				}
			}
			@include tabletLMin {
				>ul {
					overflow: hidden;
					max-height: 0;
					transition: max-height 0.66s;
				}
				&:hover {
					> ul {
						max-height: 200px;
					}
				}
			}
		}
	}
	&__logo {
		width: 100px;
		text-align: center;
		@include tabletL {
			display: none;
		}
		a {
			display: inline-block;
			vertical-align: middle;
			padding: 15px 10px;
			@include tabletLMin {
				transition: .3s;
				&:hover {
					opacity: .65;
				}
			}
			@include tabletL {
				padding:15px  0;
			}
		}
		img {
			width:65px;
		}
	}
}
