.gFoot {
	position: relative;
	z-index: 9;
	background: #fff;
	&Banner {
		background: rgba($mainColor,.8);
		padding: 50px 60px 30px;
		@include tabletP {
			padding: 25px 30px 30px;
		}
		&Slider {
			position: relative;
			max-width: 920px;
			margin-right: auto;
			margin-left: auto;
			.slick-slider {
			}
			.slick-arrow {
				position: absolute;
				top: 50%;
				width: 60px;
				height: 60px;
				margin-top: -40px;
				background-color: transparent;
				border: none;
				text-indent: -9999px;
				overflow: hidden;
				background-repeat: no-repeat;
				background-position: center;
				@include tabletP {
					width: 25px;
					background-size: 12px;
				}
				&:focus {
					outline: none;
				}
				&.slick-prev {
					left: -50px;
					background-image:url(../img/common/icon_foot_prev.svg);
					@include tabletP {
						left: -30px;
					}
				}
				&.slick-next {
					right: -50px;
					background-image:url(../img/common/icon_foot_next.svg);
					@include tabletP {
						right: -30px;
					}
				}
			}
			&__item {
				padding:0 10px;
				margin-bottom: 20px;
				@include tabletP {
					padding: 0;
				}
				a {
					position: relative;
					display: block;
					color: #fff;
					@include fz(13);
					text-shadow: rgba(0,0,0,.5) -2px 2px 3px, rgba(0,0,0,.5) -2px 2px 3px;
					@include tabletP {
						@include fz(12);
					}
					&::after {
						content: "";
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
						background: rgba(#000,.1);
					}
					> img {
						border-radius: 4px;
						vertical-align: middle;
						@include tabletP {
							width: 100%;
							height:auto;
						}
					}
					> span {
						position: absolute;
						top: 50%;
						left: 0;
						transform: translateY(-50%);
						z-index: 1;
						width: 100%;
						@include fz(18);
						text-align: center;
						text-shadow : 1px 1px 1px rgba(35, 24, 21, 0.8);
						text-overflow: ellipsis;
						white-space: nowrap;
						overflow: hidden;
					}
				}
			}
			
			.jsFootBannerSlider.-off {
				display: flex;
				flex-wrap: wrap;
				.gFootBannerSlider__item {
					width: 33.333%;
					@include tabletP {
						width: 100%;
					}
				}
			}
		}
		&Button {
			text-align: center;
			a {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				color: #fff;
				text-decoration: none;
				padding-left: 1.5em;
				&::before,
				&::after {
					content:"";
					position: absolute;
					top: 50%;
					left: 0;
					width: 1em;
					height: 1px;
					background: #fff;
					-webkit-backface-visibility:hidden;
					backface-visibility:hidden;
					opacity: 0.9;
				}
				&::after {
					transform: rotate(90deg);
					transition: .3s;
				}
				&.-off {
					&::after {
						transform: rotate(0);
						opacity: 0;
					}
				}
			}
		}
	}
	&Main {
		background: $mainColor;
		padding-top: 70px;
		padding-bottom: 40px;
		@include tabletL {
			padding-top: 30px;
			padding-bottom: 20px;
		}
	}
	&Nav {
		margin-bottom: 50px;
		@include tabletL {
			margin-bottom: 40px;
		}
		&__inner {
			display: table;
			width: calc(100% + 40px);
			margin-right: -20px;
			margin-left: -20px;
			@include tabletL {
				display: block;
				width: 100%;
				margin-right: 0;
				margin-left: 0;
			}
		}
		&__item {
			display: table-cell;
			border-left: 1px solid rgba(#fff,.3);
			padding: 0 20px;
			@include tabletL {
				display: block;
				border-left: none;
				padding: 0;
			}
			&:first-child {
				border-left: none;
				.gFootNavList:first-child {
					@include tabletL {
						border-top: 1px solid rgba(#fff,.3);
					}
				}
			}
		}
		&List {
			padding: 10px 0;
			border-bottom: 1px solid rgba(#fff,.3);
			@include tabletL {
				padding: 0;
			}
			&:first-child {
				padding-top: 0;
			}
			&:last-child {
				border-bottom: none;
				@include tabletL {
					border-bottom: 1px solid rgba(#fff,.3);
				}
			}
			&__toggle {
				position: absolute;
				display: block;
				top: 0;
				right: 0;
				width: 30px;
				height: 100%;
				cursor: pointer;
				&::before,
				&::after {
					content:" ";
					position: absolute;
					top: 50%;
					left: 50%;
					margin-top:-1px;
					margin-left: -5px;
					width: 10px;
					height: 2px;
					background: #7FA9D3;
					transition: .3s;
				}
				&::after {
					transform: rotate(-90deg);
				}
				&.-active {
					&::after {
						transform: rotate(0);
						opacity: 0;
					}
				}
				@include tabletLMin {
					display: none;
				}
			}
			&__header {
				position: relative;
				@include fz(14);
				margin-bottom: 5px;
				@include tabletL {
					margin-bottom: 0;
					@include fz(11);
				}
				&:last-child {
					margin-bottom: 0;
				}
				>span,
				a {
					color: #fff;
					text-decoration: none;
					@include tabletL {
						display: block;
						padding: 10px 5px;
					}
					&[target="_blank"] {
						position: relative;
						padding-right:15px;
						&::after {
							content: "";
							display: inline-block;
							margin-left: 5px;
							width: 10px;
							height: 10px;
							background: url(../img/common/icon_newwindow.svg) no-repeat center;
							background-size: 10px 10px;
							opacity: 0.7;
						}
					}
				}
				a:hover {
					@include tabletLMin {
						text-decoration: underline;
					}
				}
			}
			&__body {
				@include fz(12);
				@include tabletL {
					display: none;
					background: rgba(#fff,.05);
					padding: 15px 5px;
					border-top: 1px solid rgba(255,255,255,.3);
				}
				ul {
					li {
						margin-bottom: 3px;
						@include tabletL {
							margin-bottom: 5px;
						}
						>span {
							color: rgba(#fff,.7);
							text-decoration: none;
						}
						a {
							color: rgba(#fff,.7);
							text-decoration: none;
							&[target="_blank"] {
								position: relative;
								padding-right:15px;
								&::after {
									content: "";
									position: absolute;
									display: inline-block;
									top: 50%;
									right: 0;
									margin-top: -5px;
									width: 10px;
									height: 10px;
									background: url(../img/common/icon_newwindow.svg) no-repeat center;
									background-size: 10px 10px;
									opacity: 0.7;
								}
							}
							&:hover {
								@include tabletLMin {
									text-decoration: underline;
								}
							}
						}
					}
				}
				&.-column {
					@include tabletLMin {
						display: flex;
						justify-content: space-between;
					}
					> ul {
						padding:0 10px;
						&:first-child {
							padding-left: 0;
						}
						&:last-child {
							padding-right: 0;
						}
						@include tabletL {
							width: 100%;
							padding: 0;
						}
					}
				}
			}
		}
		&__bottom {
			@include fz(12);
			margin-top: 30px;
			@include tabletL {
				margin-top: 0;
				@include fz(11);
			}
			ul {
				text-align: right;
				@include tabletL {
					text-align: left;
				}
				li {
					display: inline-block;
					margin-right: 30px;
					@include tabletL {
						display: block;
						margin-right: 0;
						border-bottom: 1px solid rgba(255,255,255,.3);
					}
					&:last-child {
						margin-right: 0;
					}
					a {
						color: rgba(#fff,.7);
						text-decoration: none;
						@include tabletLMin {
							&:hover {
								text-decoration: underline;
							}
						}
						@include tabletL {
							color:#fff;
							display: block;
							padding:10px 5px;
						}
					}
				}
			}
		}
	}
	&Map {
		margin-bottom: 50px;
		@include tabletP {
			margin-bottom: 40px;
		}
		&__inner {
			position: relative;
			padding-top: 30%;
