		&__logo {
			margin-bottom: 30px;
			img {
				@include tabletP {
					width: 232px;
				}
			}
		}
		&__text {
			@include tabletP {
				@include fz(10)
			}
		}
	}
	&Copyright {
		background: #fff;
		text-align: center;
		@include fz(12);
		padding: 10px 0;
		@include tabletL {
			@include fz(11);
			padding: 15px 0;
		}
	}
}