.modTitle01 {
	@include fz(50);
	text-shadow: 1px 1px 1px rgba(#000, 0.7);
	font-weight: 500;
	color: $mainColor;
	margin-bottom: 100px;
	@include tabletP {
		@include fz(25);
	}
	&:last-child {
		margin-bottom: 0;
	}
	&__inner,
	&__spinner {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		&::after {
			content:"";
			position: absolute;
			display: block;
			left: 0;
			bottom: 0;
			height: 3px;
			width: 100%;
			background: $mainColor;
			box-shadow: 1px 1px 1px rgba(#000, 0.7);
			@include tabletP {
				height: 2px;
			}
		}
	}
	&[aria-hidden="false"] {
		.modTitle01__inner,
		.modTitle01__spinner {
			&::after {
				transition: 1s;
			}
		}
	}
	&[aria-hidden="true"] {
		 .modTitle01__inner,
		 .modTitle01__spinner {
			&::after {
				width: 0%;
			}
		}
	}
}

.modTitle02 {
	text-align: center;
	@include fz(34);
	line-height: 1.2;
	margin-bottom: 100px;
	@include tabletP {
		@include fz(20);
		margin-bottom: 50px;
	}
	.main ,
	.sub {
		display: block;
	}
	.main {
		margin-bottom: 10px;
		@include tabletP {
			margin-bottom: 5px;
		}
		&:last-child {
			margin-bottom: 0;
		}
	}
	.sub {
		@include fz(20);
		margin-bottom: 10px;
		@include tabletP {
			@include fz(12);
			margin-bottom: 5px;
		}
		&:last-child {
			margin-bottom: 0;
		}
	}
}

.modTitle03 {
	text-align: center;
	@include fz(29);
	color: $mainColor;
	line-height: 1.2;
	font-weight: 500;
	margin-bottom: 40px;
	@include tabletL {
		@include fz(20);
		margin-bottom: 30px;
	}
	@include tabletP {
		@include fz(16);
		margin-bottom: 20px;
	}
	&__inner,
	&__spinner {
		position: relative;
		&::after {
			content: " ";
			position: absolute;
			bottom: -3px;
			left: 0;
			width: 100%;
			height: 4.5px;
			background: $mainColor;
			@include tabletP {
				bottom: -1px;
				height: 3px;
			}
		}
	}
	&__spinner {
		@include tabletP {
			display: table;
			margin: 0 auto 10px;
			&:last-child {
				margin-bottom: 0;
			}
		}
		&::after {
			display: none;
		}
	}
	&.-mbNone {
		margin-bottom: 0;
	}
	&.-mbLarge {
		margin-bottom: 100px;
		@include tabletP {
			margin-bottom: 50px;
		}
	}
	&.-spline {
		@include tabletP {
			.modTitle03__inner {
				&::after {
					display: none;
				}
			}
			.modTitle03__spinner {
				&::after {
					display: block;
				}
			}
		}
	}
	&[aria-hidden="false"] {
		.modTitle03__inner,
		.modTitle03__spinner {
			&::after {
				transition: 1s;
			}
		}
	}
	&[aria-hidden="true"] {
		.modTitle03__inner,
		.modTitle03__spinner {
			&::after {
				width: 0%;
			}
		}
	}
}

.modTitle04 {
	@include fz(18);
	font-weight: 600;
	margin-bottom: 20px;
	line-height: 1.2;
	&.-bold {
		font-weight: 700;
	}
	&.-indent01 {
		position: relative;
		padding-left: 1.2em;
		.num {
			position: absolute;
			left: 0;
		}
	}
}
