.modTxtVertical {
	.column {
		display: flex;
		justify-content: center;
		flex-direction: row-reverse;
	}
	.char {
		display: table;
		margin-right: auto;
		margin-left: auto;
	}
	.vertical {
		writing-mode: vertical-rl;
	}
}

body.safari {
	.modTxtVertical {
		.vertical {
			line-height: 0;
		}
	}
}

.modTxtCenter {
	text-align: center;
}

.modTxtCatch {
	text-align: center;
	@include fz(18);
	@include tabletP {
		@include fz(12);
	}
}

.modTxtLinkNone {
	color: $baseColor;
	text-decoration: none;
}